.likes-block {
  display: flex;
  align-items: center;
  min-height: 31px;
}

.likes-block__liked {
  @include style-reset;
  @include bg-image("heart.svg", center, center, 13px 11px);
  position: relative;
  width: 31px;
  height: 31px;
  flex-shrink: 0;
  background-color: #edeff6;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: opacity 0.2s ease 0s;
  margin-right: 8px;
  z-index: 1;

  &:hover,
  &:focus {
    outline: none;
    background-image: url("../img/heart-hover.svg");
  }

  &:active {
    opacity: 0.7;
  }

  &--active {
    background-image: url("../img/heart-active.svg");
    background-color: #f9eaea;
  }
}

.likes-block__count-likes {
  @include style-reset;
  @include font-style(14px, 21px, 700, #161c35);
  text-transform: uppercase;
  flex-grow: 1;
  min-width: 0;
  word-wrap: break-word;
}

.likes-block__button {
  width: 85.2%;
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
}

@media ($breakpoints-medium) {
  .likes-block {
    min-height: 50px;
  }
  
  .likes-block__liked {
    background-size: 20px 17px;
    position: static;
    width: 50px;
    height: 50px;
    margin-right: 8px;
  }

  .likes-block__count-likes {
    @include style-reset;
    @include font-style(14px, 21px, 700, #161c35);
    text-transform: uppercase;
    flex-grow: 1;
    min-width: 0;
    word-wrap: break-word;
  }

.likes-block__button {
    width: 148px;
    position: static;
    bottom: auto;
    left: auto;
    transform: none;
  }
}