.first-letter {
  border-collapse: collapse;
  width: 100%;
  height: 247px;
}

.first-letter td {
  vertical-align: middle;
  border: 1px solid #cbced9;
  background-color: #ffffff;
  position: relative;
}

.first-letter input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;

  &:focus + label {
    background-color: #1d2e5b;
    color: #ffffff;
    border-color: #1d2e5b;
    z-index: 10;
  }

  &:disabled + label {
    cursor: auto;
    background-color: transparent;
    color: #cbced9;
    border-color: transparent;
  }
  
  &:checked + label {
    background-color: #1d2e5b;
    color: #ffffff;
    border-color: #1d2e5b;
    z-index: 10;
  }
  
  &:disabled + label:active {
    opacity: 1;
  }
}

.first-letter label {
  @include style-reset;
  @include font-style(14px, 42px, 500, #1d2e5b);
  position: absolute;
  top: -1px;
  left: -1px;
  right: -1px;
  bottom: -1px;
  display: block;
  width: calc(100% + 2px);
  height: calc(100% + 2px);
  text-align: center;
  text-transform: uppercase;
  transition: background 0.2s ease 0s, color 0.2s ease 0s, border 0.2s ease 0s, opacity 0.2s ease 0s;
  z-index: 0;
  border: 1px solid transparent;
  cursor: pointer;

  &:hover {
    background-color: #1d2e5b;
    color: #ffffff;
    border-color: #1d2e5b;
    z-index: 10;
  }

  &:active {
    opacity: 0.3;
  }
}
