.countries-list {
  @include default-list;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.counties-list__item {
  width: 100%;
  margin-bottom: 5px;
}

.countries-list input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;

  &:checked + label {
    color: $second-font-color;
  }
  
  &:hover + label,
  &:focus + label {
    color: #828699;
  }

  &:active + label {
    opacity: 0.3;
  }
}

.countries-list label {
  @include style-reset;
  @include font-style(16px, 22px, 700, #b6b9c8);
  cursor: pointer;
  transition: color 0.2s ease 0s, opacity 0.2s ease 0s;
  text-transform: uppercase;
}
