.traveler {
  margin-top: 15px;
  position: relative;
  width: 100%;
  min-height: 415px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
  border-radius: 20px;
  padding-top: 15px;
  padding-bottom: 62px;
}

.traveler__link {
  @include style-reset;
  position: absolute;
  top: 15px;
  left: 18px;
  display: block;
  width: 70px;
  height: 70px;
  transition: filter 0.2s ease 0s, opacity 0.2s ease 0s;
  z-index: 0;

  &:hover,
  &:focus {
    outline: none;
    filter: grayscale(100%);
  }

  &:active {
    opacity: 0.3;
  }
}

.traveler__link img {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.traveler__about-user {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-right: 18px;
  margin-bottom: 25px;
  z-index: 1;
}

.traveler__user-link {
  width: 135px;
  word-wrap: break-word;
  margin-left: 105px;
  min-height: 46px;
}

.traveler__likes-block {
  margin-left: 67px;
}

.traveler__tags-list {
  justify-content: flex-start;
  width: 170px;
  margin-left: 18px;
}

.traveler__container {
  position: relative;
  width: 100%;
  border-top: 1px solid #edeff6;
  padding-top: 24px;
  padding-left: 18px;
  padding-right: 18px;
  margin-bottom: 25px;
  
  &--transport {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.traveler__container-title {
  @include style-reset;
  @include font-style(15px, 18px, 400, #444444);
  position: absolute;
  top: 0;
  left: 18px;
  background-color: #ffffff;
  transform: translateY(-50%);
  padding-left: 14px;
  padding-right: 12px;
  
  &--second {
    left: auto;
    right: 10px;
  }
}

.traveler__countries-list {
  @include default-list;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.traveler__country {
  @include style-reset;
  @include font-style(15px, 18px, 500, #1d2e5b);
  width: 100%;
  min-height: 18px;
  position: relative;
  text-transform: uppercase;
  padding-left: 41px;
  margin-bottom: 10px;
  
  &::before {
    @include pseudo-element(26px, 18px);
    top: 50%;
    left: 0;
    background-size: 26px 18px;
    background-position: center;
    background-repeat: no-repeat;
    transform: translateY(-50%);
    background-color: darkorchid;
  }
}

@media ($breakpoints-medium) {
  .traveler {
    min-height: 322px;
    flex-direction: row;
    align-items: flex-start;
    flex-wrap: wrap;
    padding-top: 35px;
    padding-bottom: 26px;
  }
  
  .traveler__link {
    position: static;
    top: auto;
    left: auto;
    width: 190px;
    height: 190px;
    flex-shrink: 0;
    margin-left: 25px;
    margin-right: 30px;
    order: 0;
  }

  .traveler__link img {
    border-radius: 20px;
  }

  .traveler__about-user {
    width: 280px;
    padding-right: 0;
    margin-bottom: 0;
    order: 1;
  }

  .traveler__user-link {
    width: auto;
    margin-left: 0;
    min-height: auto;
    order: 0;
    margin-bottom: 25px;
  }

  .traveler__likes-block {
    margin-left: 0;
    width: 100%;
    order: 2;
  }

  .traveler__tags-list {
    width: 100%;
    margin-left: 0;
    margin-bottom: 50px;
    order: 1;
  }
  
  .traveler__container {
    width: 100%;
    padding-top: 24px;
    padding-left: 36px;
    padding-right: 36px;
    margin-top: 30px;
    margin-bottom: 0;
    order: 3;

    &--transport {
      border-top: none;
      width: 60px;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      flex-direction: column;
      padding: 0;
      margin: 0;
      margin-left: auto;
      margin-right: 36px;
      order: 2;
    }
  }
  
  .traveler__transports {
    margin-bottom: 15px;
  }
  
  .traveler__container-title {
    left: 71px;
  }

  .traveler__container--transport .traveler__container-title {
    display: none;
  }

  .traveler__countries-list {
    flex-direction: row;
    flex-wrap: wrap;
  }

  .traveler__country {
    @include font-style(20px, 24px, 500, #1d2e5b);
    width: auto;
    flex-shrink: 0;
    min-height: 24px;
    max-width: 220px;
    padding-left: 41px;
    margin-right: 30px;
    word-wrap: break-word;

    &::before {
      width: 35px;
      height: 24px;
      background-size: 25px 24px;
    }
    
    &:last-child {
      margin-right: 0;
    }
  }
}

@media ($breakpoints-large) {
  .traveler {
    flex-direction: column;
    height: 285px;
    min-height: auto;
    padding-top: 45px;
    padding-right: 44px;
    padding-bottom: 42px;
    padding-left: 402px;
  }
  
  .traveler__link {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 285px;
    height: 285px;
    flex-shrink: 0;
    margin-left: 0;
    margin-right: 0;
    order: 0;
  }

  .traveler__link img {
    border-top-right-radius: 0; 
    border-bottom-right-radius: 0;
  }

  .traveler__about-user {
    width: 310px;
    height: 100%;
    padding-right: 0;
    margin-bottom: 0;
    order: 1;
  }

  .traveler__user-link {
    width: 100%;
    margin-bottom: 25px;
    transform: translateX(-20px);
  }

  .traveler__likes-block {
    margin-top: auto;
    order: 2;
  }

  .traveler__tags-list {
    width: 100%;
    margin-bottom: 0;
  }
  
  .traveler__container {
    width: 237px;
    height: 98px;
    border-top: none;
    padding: 0;
    margin: 0;
    order: 2;
    overflow: hidden;

    &--transport {
      width: 217px;
      height: 60px;
      flex-direction: row;
      justify-content: space-between;
      padding: 0;
      margin: 0;
      margin-top: auto;
      margin-left: 0;
      margin-right: 0;
      order: 3;
    }
  }
  
  .traveler__transports {
    margin-bottom: 0;
  }
  
  .traveler__container-title {
    display: none;
  }

  .traveler__countries-list {
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: flex-start;
    height: 98px;
    width: 100%;
    overflow: hidden;
  }

  .traveler__country {
    max-width: none;
    padding-left: 55px;
    margin-right: 0;
    word-wrap: normal;
    overflow: hidden;
  }
}