.transports {
  @include default-list;
  max-width: 240px;
  min-height: 21px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.transports__item {
  flex-shrink: 0;
  margin-right: 15px;
  margin-bottom: 10px;

  &--active .transports__type {
    opacity: 1;
  }

  &:last-child {
    margin-right: 0;
  }
}

.transports__type {
  display: block;
  height: 21px;
  font-size: 0;
  opacity: 0.3;

  &--plain {
    @include bg-image("plane.svg", center, center, auto);
    width: 19px;
  }

  &--bus {
    @include bg-image("bus.svg", center, center, auto);
    width: 17px;
    transform: translateX(2px);
  }

  &--bicycle {
    @include bg-image("bike.svg", center, center, auto);
    width: 24px;
  }

  &--walk {
    @include bg-image("run.svg", center, center, auto);
    width: 20px;
    transform: translateX(-4px);
  }
}

@media (max-width: 767px) {
  .transports--traveler .transports__item {
    width: 32px;
    height: 32px;
    background-color: #edeff6;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
  }

  .transports--traveler .transports__type {
    height: 16px;

    &--plain {
      width: 14px;
    }

    &--bus {
      width: 13px;
      transform: none;
    }

    &--bicycle {
      height: 17px;
      width: 19px;
    }

    &--walk {
      height: 17px;
      width: 15px;
      transform: none;
    }
  }
}

@media ($breakpoints-medium) {
  .transports--traveler {
    @include default-list;
    max-width: none;
    min-height: 117px;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    flex-wrap: nowrap;
  }
  
  .transports--traveler .transports__item {
    margin-right: 0;
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .transports--traveler .transports__type {
    height: 21px;
  
    &--plain {
      width: 19px;
    }

    &--bus {
      width: 17px;
      transform: none;
    }

    &--bicycle {
      width: 24px;
    }

    &--walk {
      width: 20px;
      transform: none;
    }
  } 
}

@media ($breakpoints-large) {
  .transports--traveler {
    width: 160px;
    max-width: none;
    min-height: auto;
    flex-direction: row;
    flex-wrap: wrap;
    overflow: hidden;
  }
  
  .transports--traveler .transports__item {
    margin-right: 10px;
    margin-bottom: 0;

    &:last-child {
      margin-right: 0;
    }
  }

  .transports--traveler .transports__type {
    height: 21px;
  
    &--plain {
      width: 19px;
    }

    &--bus {
      width: 17px;
      transform: none;
    }

    &--bicycle {
      width: 24px;
    }

    &--walk {
      width: 20px;
      transform: none;
    }
  } 
}
