.content-header {
  position: relative;
  background-color: #161c35;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  z-index: 3;
}

.content-header__wrapper {
  width: 100%;
  padding: 25px 25px 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 500px;
  margin: 0 auto;
}

.content-header__title {
  @include style-reset;
  @include font-style(36px, 40px, 700, #ffffff);
  display: inline-block;
  position: relative;
  padding-bottom: 6px;

  &::after {
    @include pseudo-element(100%, 5px);
    border-radius: 2.39px;
    background-color: #ff8d30;
    bottom: -8px;
    left: 0;
    right: 0;
  }
}

.content-header__link {
  display: none;
}
