.logo {
  @include style-reset;
  @include bg-image("logo-mobile-blue@1x.png", center, center);
  display: block;
  width: 100px;
  height: 19px;
  font-size: 0;
  transition: filter 0.1s ease 0s, opacity 0.2s ease 0s;
}

.logo[href] {
  &:hover,
  &:focus {
    filter: drop-shadow(2px 3px 3px rgba(0, 0, 0, 0.5));
    outline: none;
  }
  
  &:active {
    opacity: 0.7;    
  }
}

@media ($breakpoints-medium) {
  .logo {
    width: 164px;
    height: 42px;
    background-image: url("../img/logo-tablet-blue@1x.png");
  }
}

@media ($breakpoints-large) {
  .logo {
    width: 200px;
    height: 52px;
    background-image: url("../img/logo-desktop-white@1x.png");

    &--footer {
      background-image: url("../img/logo-desktop-blue@1x.png");
    }
  }
}
