.travelers__list {
  @include default-list;
  width: 100%;
  padding-left: 25px;
  padding-right: 25px;
}

.travelers__item {
  width: 100%;
  min-height: 415px;
  max-width: 500px;
  margin: 0 auto;
}

@media ($breakpoints-medium) {
  .travelers__list {
    padding-left: 40px;
    padding-right: 40px;
  }

  .travelers__item {
    min-height: 332px;
    width: 688px;
    max-width: none;
  }
}

@media ($breakpoints-large) {
  .travelers__list {
    width: 951px;
    min-width: auto;
    padding-left: 0;
    padding-right: 0;
    margin: 0  auto;
  }

  .travelers__item {
    min-height: auto;
    height: 285px;
    width: 951px;
  }
}