.cities {
  @include default-list;
  display: none;
  margin-bottom: 15px;
  &--checked {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}

.cities__list {
  @include default-list;
}

.cities__list a { 
  @include style-reset;
  @include font-style(16px, 22px, 400, #444444);
  transition: color 0.2s ease 0s, opacity 0.2s ease 0s;

  &:hover,
  &:focus {
    color: #1d2e5b;
  }
  
  &:active {
    opacity: 0.3;
  }
}

.cities__letter {
  display: none;
}