.user-link {
  @include style-reset;
  display: block;
  max-width: 240px;
  min-height: 23px;
  transition: opacity 0.2s;

  &:hover,
  &:focus {
    opacity: 0.7;
    outline: none;
  }

  &:active {
    opacity: 0.3;
  }
}

.user-link__title {
  @include style-reset;
  @include font-style(21px, 23px, 700, #1d2e5b);
  display: block;
  position: relative;
  padding-left: 17px;

  &::before {
    @include pseudo-element(5px, 5px);
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    background-color: #f97676;
    border-radius: 50%;
  }

  &--online::before {
    background-color: #81e31e;
  }
}


@media ($breakpoints-medium) {
  .user-link__title {
    @include font-style(24px, 26px, 700, #1d2e5b);
    padding-left: 21px;

    &::before {
      @include pseudo-element(7px, 7px);
    }
  }
  
  .user-link--traveler .user-link__title {
    padding-right: 25px;
  }
}

@media (max-width: 1339px) {
  .user-link--traveler .user-link__title {
    padding-left: 0;
    padding-right: 50px;

    &::before {
      left: auto;
      right: 0;
    }
  }

  @media ($breakpoints-medium) {
    .user-link--traveler .user-link__title {
      padding-right: 25px;
    }
  }
}

