.level {
  @include style-reset;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 60px;
  height: 60px;
  background-color: #0b151a;
  border-radius: 50%;

  &--traveler {
    background-color: #ffffff;
  }
}

.level--80 .level__pie {
  @include level-max(80);
}

.level--99 .level__pie {
  @include level-max(99);
}

.level--25 .level__pie {
  @include level-less(25);
}

.level--50 .level__pie {
  @include level-less(50);
}

.level__pie {
  position: absolute;
  left: 0;
  top: 0;
  width: 60px;
  height: 60px;
  clip: rect(0, 60px, 60px, 30px);
}

.level__half-circle {
  position: absolute;
  left: 0;
  top: 0;
  width: 60px;
  height: 60px;
  border: 3px solid #ff8d30;
  border-radius: 50%;
  clip: rect(0, 30px, 60px, 0);
}

.level__numb {
  @include style-reset;
  @include font-style(24px, 24px, 700);
  display: block;
  height: 24px;
  width: 38px;
  overflow: hidden;
  text-align: center;
  margin-top: 13px;
}

.level--traveler .level__numb{
  color: #1d2e5b;
}

.level__text {
  @include style-reset;
  @include font-style(14px, 14px, 400);
  width: 31px;
  height: 17px;
  padding-top: 2px;
  display: block;
  border-top: 1px solid rgba(255, 255, 255, 0.3);
  text-align: center;
}

.level--traveler .level__text {
  color: #444444;
  border-top: 1px solid rgba(229, 229, 229, 0.8);
  opacity: 0.4;
}

@media (max-width: 767px) {
  .level--traveler .level__numb {
    margin-top: 17px;
  }

  .level--traveler .level__text {
    display: none;
  }
}
